.aws-btn {
    --slider-height-percentage: 60%;
    --slider-transition-duration: 700ms;
    --organic-arrow-thickness: 4px;
    --organic-arrow-border-radius: 0px;
    --organic-arrow-height: 40px;
    --organic-arrow-color: #ffffff !important;
    --control-button-width: 26%;
    --control-button-height: 36%;
    --control-button-background: transparent;
    --control-bullet-color: #fafcff;
    --control-bullet-active-color: #ffffff;
    --loader-bar-color: #ca2f2f;
    --loader-bar-height: 5px;
    }
    
  
      .aws-sld {
        &--exit {
          &.aws-sld--moveRight, &.aws-sld--moveLeft {
            animation: scaleOut var(--slider-transition-duration) both
              var(--transition-bezier);
          }
        }
      }
      
      @keyframes scaleOut {
        to {
          transform: scale(0.85);
        }
      }